<template>
  <v-container
    fluid
    grid-list-xl>

    <!-- 錯誤訊息 -->
    <CommonErrorMsg
      :show.sync="error.show"
      :message.sync="error.message"
    />

    <!-- 標題 & 返回按鈕 -->
    <v-layout
      row
      wrap>
      <v-flex
        md6
        tag="h3"
        class="mt-0">
        {{ $ln('修改子帳號') }}
      </v-flex>
      <v-flex
        md6
        class="text-xs-right">
        <v-btn
          small
          color="amber darken-3"
          @click="toBack()">
          <v-icon class="mr-2">
            mdi-arrow-left
          </v-icon>
          {{ $ln('返回') }}
        </v-btn>
      </v-flex>
    </v-layout>

    <v-layout
      row
      wrap>
      <v-flex
        xs6>
        <!-- 修改密碼 -->
        <AccountManagementEditPasswordIndex
          :account="subAccountItems.account"
          :update-type="'others'"
          :account-type="'hlead'"
        />
      </v-flex>

      <v-flex
        xs6
        class="text-xs-right">
        <v-btn
          v-if="subAccountItems.valid && !modifyingStatus"
          small
          color="deep-orange darken-1"
          @click="edit()">
          {{ $ln('確定修改') }}
        </v-btn>
        <v-btn
          v-else-if="!subAccountItems.valid || modifyingStatus"
          :loading="modifyingStatus"
          disabled
          small
          color="grey">
          {{ $ln('確定修改') }}
        </v-btn>
      </v-flex>
    </v-layout>

    <material-card>
      <v-form v-model="subAccountItems.valid">
        <v-layout
          row
          wrap>
          <v-flex
            md4
            xs12>
            <!-- 帳號 -->
            <v-text-field
              v-model="subAccountItems.account"
              :label="$ln('帳號')"
              disabled
            />
          </v-flex>

          <v-flex
            md4
            xs12>
            <!-- 名稱 -->
            <v-text-field
              v-model="subAccountItems.nameItems.name"
              :rules="subAccountItems.nameItems.nameRules"
              :label="$ln('名稱')"
            />
          </v-flex>

          <v-flex
            md4
            xs12>
            <!-- 身份 -->
            <v-select
              v-model="subAccountItems.identity.selected"
              :items="subAccountItems.identity.options"
              :label="$ln('身份')"
              required>
              <template slot='selection' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
              <template slot='item' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
            </v-select>
          </v-flex>
        </v-layout>

        <v-layout
          row
          wrap>
          <!-- 是否允許登入 -->
          <v-flex
            md4
            xs6>
            <div class="v-input__slot">
              <div class="v-text-field__slot">
                <label
                  aria-hidden="true"
                  class="v-label theme--light"
                  style="left: 0px; right: auto; position: absolute;">{{ $ln('是否允許登入') }}</label>
                <v-radio-group
                  v-model="subAccountItems.whetherAllowLogin.selected"
                  row>
                  <v-radio
                    class="v-input-checkbox"
                    v-for="(item, i) in subAccountItems.whetherAllowLogin.options"
                    :key="i"
                    :label="item.text"
                    :value="item.value"/>
                </v-radio-group>
              </div>
            </div>
          </v-flex>

          <!-- 開放Super彩球 -->
          <v-flex
            md4
            xs6
            v-show="subAccountItems.openSuperLottery.show">
            <div class="v-input__slot">
              <div class="v-text-field__slot">
                <label
                  aria-hidden="true"
                  class="v-label"
                  style="left: 0px; right: auto; position: absolute;">{{ $ln('開放Super彩球') }}</label>
                <v-radio-group
                  v-model="subAccountItems.openSuperLottery.selected"
                  row>
                  <v-radio
                    class="v-input-checkbox"
                    v-for="(item, i) in subAccountItems.openSuperLottery.options"
                    :key="i"
                    :label="item.text"
                    :value="item.value"/>
                </v-radio-group>
              </div>
            </div>
          </v-flex>

          <!-- 備註 -->
          <v-flex
            md4
            xs12>
            <v-textarea
              v-model="subAccountItems.remark.contents"
              :label="$ln('備註')"
            />
          </v-flex>
        </v-layout>
      </v-form>
    </material-card>

    <div class="pt-2 text-xs-right">
      <v-btn
        color="blue darken-1"
        flat
        small
        @click="toBack()">
        {{ $ln('取消') }}
      </v-btn>

      <v-btn
        v-if="subAccountItems.valid && !modifyingStatus"
        small
        color="deep-orange darken-1"
        @click="edit()">
        {{ $ln('確定修改') }}
      </v-btn>
      <v-btn
        v-else-if="!subAccountItems.valid || modifyingStatus"
        :loading="modifyingStatus"
        disabled
        small
        color="grey">
        {{ $ln('確定修改') }}
      </v-btn>
    </div>

  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { common } from '@/utils'

export default {
  name: 'EditSubAccount',
  data () {
    return {
      // 錯誤訊息
      error: {
        show: false,
        message: '',
      },
      // 修改子帳號資料
      subAccountItems: {
        dialogOpen: false,
        valid: true,
        id: '',
        // 帳號
        account: '',
        // 名稱資料
        nameItems: {
          // 名稱
          name: '',
          nameRules: [] // *initLanguage*
          //['不能為空格', '不能為特殊符號或空格，除了「括號()」、「底線_」和「-」']
        },
        // 是否允許登入(0：允許登入 1：不允許登入)
        whetherAllowLogin: {
          selected: '',
          options: [] // *initLanguage*
          //['是', '否']
        },
        // 開放Super彩球(0：開放 1：不開放)
        openSuperLottery: {
          show: true,
          selected: '',
          options: [] // *initLanguage*
          //['是', '否']
        },
        // 身份
        identity: {
          selected: '',
          options: [
            {
              text: '查詢員',
              value: '查詢員'
            }
          ]
        },
        // 備註
        remark: {
          contents: ''
        }
      },
      // 修改中狀態
      modifyingStatus: false
    }
  },
  computed: {
    ...mapGetters({
      getDashboardInfoState: 'account-management/getDashboardInfoState'
    })
  },
  watch: {
    '$store.state.lang' (value) {
      this.initLanguage()
    }
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  created () {
    if (common.isObject(this.$route.query.item)) {
      /* 設定當前編輯資料 */
      // ID
      if (this.$route.query.item.id) this.subAccountItems.id = this.$route.query.item.id
      // 帳號
      if (this.$route.query.item.l_no) this.subAccountItems.account = this.$route.query.item.l_no
      // 名稱
      if (this.$route.query.item.l_name) this.subAccountItems.nameItems.name = this.$route.query.item.l_name
      // 身份
      if (this.$route.query.item.status) this.subAccountItems.identity.selected = this.$route.query.item.status
      // 是否允許登入
      if (this.$route.query.item.is_close === this.$ln('允許登入')) this.subAccountItems.whetherAllowLogin.selected = 0
      else this.subAccountItems.whetherAllowLogin.selected = 1
      // 開放Super彩球
      if (this.$route.query.item.open_super === 0) this.subAccountItems.openSuperLottery.selected = 0
      else this.subAccountItems.openSuperLottery.selected = 1
      // 備註
      if (this.$route.query.item.remark) this.subAccountItems.remark.contents = this.$route.query.item.remark
    } else {
      // 無物件資料，則轉跳上一頁
      this.toBack()
    }
  },
  mounted () {
    // 編輯頁跳回列表-記憶頁數
    if (this.$route.query.page) sessionStorage.setItem('account.list.page', this.$route.query.page)
    // 編輯頁跳回列表-記憶每頁筆數
    if (this.$route.query.rowsPerPage) sessionStorage.setItem('account.list.rowsPerPage', this.$route.query.rowsPerPage)

    this.initLanguage()
    this.updateDashboardInfo()
  },
  methods: {
    ...mapActions({
      putSubAccount: 'api/putSubAccount'
    }),
    toBack () {
      this.$router.push({
        path: '/manage/account-management/sub-account-management',
        query: {
          pageType: 'edit'
        }
      })
    },
    // 設定錯誤訊息
    showError (msg) {
      this.error.show = true
      this.error.message = msg
    },
    edit () {
      // 修改中狀態(開始)
      this.modifyingStatus = true

      let editData = {
        l_no: this.subAccountItems.account,
        l_name: this.subAccountItems.nameItems.name,
        is_close: this.subAccountItems.whetherAllowLogin.selected,
        remark: this.subAccountItems.remark.contents
      }
      // 開放Super彩球
      if (this.subAccountItems.openSuperLottery.show) editData.open_super = this.subAccountItems.openSuperLottery.selected
      try {
        this.putSubAccount(editData)
          .then(res => {
            setTimeout(() => {
              if (Object.prototype.toString.call(res) === '[object Error]' &&
                  res.hasOwnProperty('response') &&
                  res.response.status !== 200) {
                // 錯誤訊息
                if (res.response.data.hasOwnProperty('errors')) {
                  common.errorMsgHandler(res.response.data.errors)
                } else {
                  // this.$toastr.error(`${this.$ln('修改失敗')}`)

                  this.showError(this.$ln(`修改失敗`))
                }
              } else if (res.status === 200) { // success
                // this.$toastr.success((`${this.$ln('修改成功')}`), '', {timeOut: 2000, closeButton: false})
                this.showError(this.$ln(`修改成功`))

                // 修改成功回上一頁
                this.toBack()
              }

              // 修改中狀態(結束)
              this.modifyingStatus = false
            }, 1000)
          })
          .catch(err => {
            setTimeout(() => {
              // this.$toastr.error(`${this.$ln('修改失敗')}`)
              this.showError(this.$ln(`修改失敗`))

              // 修改中狀態(結束)
              this.modifyingStatus = false
            }, 1000)
          })
      } catch (error) {
        setTimeout(() => {
          // this.$toastr.error(`${this.$ln('站台資料錯誤，請聯絡客服')}`)
          this.showError(this.$ln(`站台資料錯誤，請聯絡客服`))

          // 修改中狀態(結束)
          this.modifyingStatus = false
        }, 1000)
      }
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo && dashboardInfo.hasOwnProperty('noOpenGame')) {
        // 不開放遊戲設定
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      switch (type) {
        // 關閉Super彩球
        case 's17':
          this.subAccountItems.openSuperLottery.show = false
          break
      }
    },
    // 初始語系
    initLanguage () {
      // 名稱資料
      this.subAccountItems.nameItems.nameRules = [
        v => !!v || this.$ln('不能為空格'),
        v => ((/^[\u4e00-\u9fa5_A-Za-z0-9\(\)\_\-]+$/.test(v)) || v === '') || this.$ln('不能為特殊符號或空格，除了「括號()」、「底線_」和「-」'),
        v => v.length <= 20 || this.$ln('不能超過 20 個字元')
      ]
      // 是否允許登入(0：允許登入 1：不允許登入)
      this.subAccountItems.whetherAllowLogin.options = [
        {text: this.$ln('是'),value: 0},
        {text: this.$ln('否'),value: 1}
      ]
      // 開放Super彩球(0：開放 1：不開放)
      this.subAccountItems.openSuperLottery.options = [
        {text: this.$ln('是'),value: 0},
        {text: this.$ln('否'),value: 1}
      ]
    }
  }
}
</script>

<style lang="css">
</style>
